
import { defineComponent, PropType, ref } from 'vue'
import { IProductImageForm } from '@/types'

export interface IData {
  model: IProductImageForm[];
  isValid: boolean;
}

export default defineComponent({
  name: 'ProductImageForm',

  setup () {
    return {
      form: ref()
    }
  },

  props: {
    modelValue: Object as PropType<IProductImageForm[]>,

    isLoading: Boolean as PropType<boolean>
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      isValid: false
    }
  },

  methods: {
    addImage () {
      this.model.push({
        image: null,
        sortOrder: 0
      })
    },

    removeImage (index: number) {
      this.model.splice(index, 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
