
import { defineComponent, PropType, ref } from 'vue'
import { IProductOptionValueForm, IOptionValueItem } from '@/types'
import useFetching from '@/hooks/useFetching'
import { useStore } from 'vuex'

export interface IData {
  model: IProductOptionValueForm[];
  prefixList: string[];
  booleanList: Array<{
    id: boolean;
    name: string;
  }>;
  isValid: boolean;
  optionValues: IOptionValueItem[];
}

export default defineComponent({
  name: 'ProductOptionValueForm',

  setup () {
    const store = useStore()
    const [loadValues, status, error] = useFetching((id: number) => {
      return store.dispatch('option/loadValueItems', {
        id: id,
        payload: {
          fields: 'id,name'
        }
      })
    })

    return {
      form: ref(),
      loadValues,
      status,
      error
    }
  },

  props: {
    modelValue: Array as PropType<IProductOptionValueForm[]>,

    optionId: Number as PropType<number>,

    isLoading: Boolean as PropType<boolean>
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      prefixList: ['+', '-'],

      booleanList: [
        {
          id: true,
          name: 'main.buttons.yes'
        },
        {
          id: false,
          name: 'main.buttons.no'
        }
      ],

      optionValues: [],

      isValid: false
    }
  },

  async mounted (): Promise<void> {
    this.optionValues = await this.loadValues(this.optionId)
  },

  methods: {
    handleAdd () {
      if (!this.optionId) {
        return
      }

      this.model.push({
        optionId: this.optionId,
        optionValueId: null,
        quantity: 0,
        isSubtract: false,
        price: '0.00',
        pricePrefix: '+',
        points: 0,
        pointsPrefix: '+',
        weight: '0.00000000',
        weightPrefix: '+'
      })
    },

    handleRemove (index: number) {
      this.model.splice(index, 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
