
import { defineComponent, PropType } from 'vue'

export interface Item {
  [prop: string]: any;
}

export default defineComponent({
  name: 'ElAutocompleteList',

  props: {
    modelValue: Array as PropType<Item[]>,

    itemValue: {
      type: String as PropType<string>,
      default: 'id'
    },

    itemLabel: {
      type: String as PropType<string>,
      default: 'name'
    },

    placeholder: {
      type: String as PropType<string>,
      default: 'Search'
    },

    height: {
      type: String as PropType<string>,
      default: '300px'
    },

    search: Function as PropType<(queryString: string, cb: (arg: any) => void) => Promise<Item[]>>
  },

  watch: {
    modelValue: {
      handler: function (value: Item[]) {
        if (JSON.stringify(value) !== JSON.stringify(this.model)) {
          this.model = value
          this.checked = this.model.map((item: Item) => item[this.itemValue])
        }
      },
      deep: true
    },

    model: {
      handler: function (value: Item[]) {
        this.checked = this.model.map((item: Item) => item[this.itemValue])

        if (JSON.stringify(value) !== JSON.stringify(this.modelValue)) {
          this.$emit('update:modelValue', value)
        }
      },
      deep: true
    }
  },

  data () {
    return {
      model: [] as Item[],
      checked: [] as number[],
      currentItem: null as string | null
    }
  },

  methods: {
    handleChecked (value: number[]) {
      this.model = this.model.filter((item) => value.includes(item[this.itemValue]))
    },

    handleSelect (item: Item) {
      if (item[this.itemValue]) {
        if (this.model.find((m) => m[this.itemValue] === item[this.itemValue])) {
          return
        }

        this.model.push({
          [this.itemValue]: item[this.itemValue], // id, value
          [this.itemLabel]: item[this.itemLabel] // name, label
        })
      }
    }
  }
})
