
import { defineComponent, PropType, ref } from 'vue'
import { IProductDiscountForm, ICustomerGroupItem } from '@/types'

export interface IData {
  model: IProductDiscountForm[];
  isValid: boolean;
}

export default defineComponent({
  name: 'ProductDiscountForm',

  setup () {
    return {
      form: ref()
    }
  },

  props: {
    modelValue: Array as PropType<IProductDiscountForm[]>
  },

  computed: {
    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      isValid: false
    }
  },

  methods: {
    addDiscount () {
      this.model.push({
        customerGroupId: null,
        quantity: 0,
        priority: 0,
        price: '0.00',
        startedAt: new Date().toISOString(),
        endedAt: new Date().toISOString()
      })
    },

    removeDiscount (index: number) {
      this.model.splice(index, 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
