
import { defineComponent, PropType, ref } from 'vue'
import { IProductOptionForm, IOptionItem, IOptionType } from '@/types'
import ProductOptionValueForm from '@/views/catalog/product/forms/ProductOptionValueForm.vue'

export interface IData {
  model: IProductOptionForm[];
  isValid: boolean;
  currentId: number | null;
  menuShow: boolean;
}

export default defineComponent({
  name: 'ProductOptionForm',

  setup () {
    return {
      form: ref()
    }
  },

  components: {
    ProductOptionValueForm
  },

  props: {
    modelValue: Array as PropType<IProductOptionForm[]>,

    isLoading: Boolean as PropType<boolean>
  },

  computed: {
    options (): IOptionItem[] {
      return this.$store.getters['option/getItems']
    },

    optionTypes (): IOptionType[] {
      return this.$store.getters['option/getTypes']
    },

    allowValueTypes (): string[] {
      const types = this.optionTypes.filter((t) => t.hasValues)

      return types.map((t) => {
        return t.id
      })
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      isValid: false,

      currentId: null,

      menuShow: false
    }
  },

  methods: {
    handleAdd () {
      const option = this.options.find((o) => o.id === this.currentId)

      console.log(option)

      if (option) {
        this.model.push({
          option: option,
          optionId: option.id,
          value: '',
          required: false,
          values: []
        })

        this.menuShow = !this.menuShow
      }
    },

    handleRemove (targetName: string) {
      const [, index] = targetName.split('_')

      this.model.splice(parseInt(index), 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
