
import { defineComponent, PropType, ref } from 'vue'
import { IProductAttributeForm, IAttributeGrouped } from '@/types'

export interface IData {
  model: IProductAttributeForm[];
  isValid: boolean;
}

export default defineComponent({
  name: 'ProductAttributeForm',

  setup () {
    return {
      form: ref()
    }
  },

  props: {
    modelValue: Array as PropType<IProductAttributeForm[]>
  },

  computed: {
    attributes (): IAttributeGrouped[] {
      return this.$store.getters['attribute/getGrouped'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      isValid: false
    }
  },

  methods: {
    addAttribute () {
      this.model.push({
        attributeId: null,
        value: ''
      })
    },

    removeAttribute (index: number) {
      this.model.splice(index, 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
