
import { defineComponent, PropType, ref } from 'vue'
import { ICustomerGroupItem, IProductRewardForm } from '@/types'

export interface IData {
  model: IProductRewardForm[];
  isValid: boolean;
}

export default defineComponent({
  name: 'ProductRewardForm',

  setup () {
    return {
      form: ref()
    }
  },

  props: {
    modelValue: Object as PropType<IProductRewardForm[]>,

    isLoading: Boolean as PropType<boolean>
  },

  computed: {
    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || [],

      isValid: false
    }
  },

  methods: {
    addReward () {
      this.model.push({
        customerGroupId: null,
        points: 0
      })
    },

    removeReward (index: number) {
      this.model.splice(index, 1)
    },

    async validate (): Promise<boolean> {
      if (this.form) {
        await this.form.validate()
      }

      return this.isValid
    }
  }
})
