
import { defineComponent, PropType, reactive, ref } from 'vue'
import {
  IAttributeGrouped,
  ICategoryItem,
  ICustomerGroupItem,
  IFilterGrouped,
  IProductForm,
  IProductStatusItem
} from '@/types'
import type { FormInstance, FormRules } from 'element-plus'
import ProductAttributeForm from '@/views/catalog/product/forms/ProductAttributeForm.vue'
import ProductDiscountForm from '@/views/catalog/product/forms/ProductDiscountForm.vue'
import ProductImageForm from '@/views/catalog/product/forms/ProductImageForm.vue'
import ProductOptionForm from '@/views/catalog/product/forms/ProductOptionForm.vue'
import ProductRewardForm from '@/views/catalog/product/forms/ProductRewardForm.vue'
import ProductSpecialForm from '@/views/catalog/product/forms/ProductSpecialForm.vue'
import useFetching from '@/hooks/useFetching'
import { useStore } from 'vuex'
import ElAutocompleteList from '@/components/ElAutocompleteList.vue'

export interface IData {
  model: IProductForm;
  section: 'general' | 'data';
}

export default defineComponent({
  name: 'ProductForm',

  setup () {
    const store = useStore()
    const [loadRelates, relatesStatus] = useFetching((payload: object) => {
      return store.dispatch('product/loadItems', payload)
    })

    const [loadData, status, error] = useFetching(() => {
      return store.dispatch('option/loadItems')
    })

    return {
      form: ref<FormInstance>(),
      rules: reactive<FormRules>({
        name: [
          { required: true, message: 'Please input Activity name', trigger: 'blur' }
        ]
      }),
      categoryProps: reactive({
        id: 'id',
        label: 'name',
        children: 'children',
        isLeaf: 'isLeaf'
      }),
      loadRelates,
      relatesStatus,
      loadData,
      status,
      error
    }
  },

  components: {
    ElAutocompleteList,
    ProductImageForm,
    ProductDiscountForm,
    ProductAttributeForm,
    ProductOptionForm,
    ProductRewardForm,
    ProductSpecialForm
  },

  props: {
    modelValue: Object as PropType<IProductForm>,

    isLoading: Boolean as PropType<boolean>
  },

  computed: {
    categories (): ICategoryItem[] {
      return this.$store.getters['category/getTree'] || []
    },

    filters (): IFilterGrouped[] {
      return this.$store.getters['filter/getGrouped'] || []
    },

    productStatuses (): IProductStatusItem[] {
      return this.$store.getters['productStatus/getItems'] || []
    },

    manufacturers (): object[] {
      return this.$store.getters['manufacturer/getItems'] || []
    },

    relates (): object[] {
      return this.$store.getters['product/getItems'] || []
    },

    attributes (): IAttributeGrouped[] {
      return this.$store.getters['attribute/getGrouped'] || []
    },

    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        manufacturerId: null,
        productStatusId: null,
        name: '',
        description: null,
        tags: null,
        metaTitle: '',
        metaDescription: null,
        metaKeyword: null,
        model: '',
        sku: '',
        location: '',
        price: '0.00',
        image: '',
        quantity: 999,
        minimum: 1,
        points: 0,
        weight: '0.00000000',
        weightClassId: null,
        length: '0.00000000',
        width: '0.00000000',
        height: '0.00000000',
        lengthClassId: null,
        isShipping: false,
        isSubtract: false,
        sortOrder: 0,
        isActive: true,
        availableAt: new Date().toISOString(),
        filters: [],
        categories: [],
        relates: [],
        images: [],
        rewards: [],
        discounts: [],
        attributes: [],
        options: [],
        specials: []
      },

      section: 'general'
    }
  },

  mounted (): void {
    this.loadData()
    this.loadRelates({
      fields: 'id,name',
      order: 'name:asc',
      limit: 5
    })
  },

  methods: {
    async relatesSearchAsync (queryString: string, cb: (arg: any) => void) {
      if (queryString.length > 0 && queryString !== 'null') {
        await this.loadRelates({
          fields: 'id,name',
          order: 'name:asc',
          name: queryString,
          limit: 10
        })

        return this.relates
      }

      return this.relates
    }
  }
})
